<script>
import {mapActions, mapState} from "vuex";
import NoData from "../../../components/base/NoData";
import { priceMixin } from '@/mixins/formatter.js';
import { loaderMixin } from '@/mixins/dateLoaderMixins.js'
import DeleteModal from '@/components/modals/DeleteModal.vue'

export default {
    mixins: [loaderMixin, priceMixin],
    components: {
        NoData, DeleteModal
    },
    data() {
        return {
            page: 1,
            pageCount: 0,
            accessoryItems: [],
            accessoryItemsSecondary: [],
            editModal: false,
            deleteModal: false,
            headers2: [
                {text: 'Turi', value: 'type', align: 'center'},
                {text: 'Nomi', value: 'name', align: 'center'},
                {text: 'Kod', value: 'code', align: 'center'},
                {text: 'Adadi', value: 'amount', align: 'center'},
                {text: 'Harakatlar', value: 'actions', align: 'center'},
            ],
        }
    },
    watch: {
        page: async function (newVal, oldVal) {
            if (newVal) {
                await this.refresh()
            }
        },
    },
    async mounted() {
        await this.refresh();
    },
    computed: {
        ...mapState({
            exist_products: state => state.warehouse.exist_products
        })
    },
    methods: {
        ...mapActions({
            existProduct: 'warehouse/fetchExistProducts'
        }),
        async refresh() {
            this.loading = true;
            await this.existProduct({page: this.page, storage_type: 2, status: 4})

            this.page = this.exist_products.current_page;
            this.pageCount = Math.ceil(this.exist_products.total / 20);

            this.accessoryItemsSecond = this.exist_products.data.slice(10, this.exist_products.data.length);
            this.accessoryItems = this.exist_products.data;

            this.loading = false;
        },
        closeEditModal(){
            this.editModal = false
        },

    }
}
</script>

