                </template>

                <template v-slot:item.amount="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ formatPrice(item.amount) }} kg</span>
                    </div>
                </template>

                <template v-slot:item.sum="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ $helpers.formatPrice(item.total_price) }} so`m</span>
                    </div>
                </template>

                <template v-slot:item.date="{item}">
                    <div class="d-flex justify-center">
                        <span class="text-black">{{ $dayjs(item.created_at).format('DD-MM-YYYY') }}</span>
                    </div>
                </template>

                <template v-slot:no-data>
                    <div>
                        <no-data class="mt-6"></no-data>
                        <br>
                        <h2>Ma'lumotlar topilmadi!</h2>
